body {
  margin: 0;
}

.stars {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.stars__star {
  box-sizing: border-box;
  height: 16px;
  width: 16px;
  background-image: url(./images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 4px;
}

.stars__star:last-child {
  padding-right: 0;
}

.stars--1 li:nth-child(-n + 1),
.stars--2 li:nth-child(-n + 2),
.stars--3 li:nth-child(-n + 3),
.stars--4 li:nth-child(-n + 4),
.stars--5 li:nth-child(-n + 5) {
  background-image: url(./images/star-active.svg);
}

